import React from 'react';

//BOOTSTRAP
import { Navbar, Nav, NavDropdown} from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from "react-router-dom";

//COMPONENTE
import CartWidget from "../CartWidget/CartWidget";
import NavLogo from "../NavLogo/NavLogo";

function NavBar(props) {
  return (
    <div>
        <Navbar bg="light" expand="lg">
          <Link className="Link" to="Inicio">
           <NavLogo/>
          </Link>
          <Navbar.Brand href="#Home">Codiciados</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="mr-auto">
              <Nav.Link className="Link" to="Inicio" >Inicio</Nav.Link>
              <Nav.Link href="#Sabores">Sabores</Nav.Link>
              <Nav.Link href="#About">About</Nav.Link>
              <NavDropdown title="Categorias" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Simples</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">Triples</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">Combos</NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">Postres</NavDropdown.Item>
              </NavDropdown>
            </Nav>
            <Link className="Link" to="Cart" href="Cart">
              <CartWidget/>
            </Link>
          </Navbar.Collapse>
        </Navbar>
    </div>
  );
}

export default NavBar;

